import React from "react";
import { Model } from "./models";

export class OrbitLayer extends React.Component {
    constructor() {
        super();
        this.originX = 1.5;
        this.originY = 7;
        this.finalX = 13;
        this.finalY = 1;
        this.stepX = (this.finalX - this.originX) / 100.0;
        this.stepY = (this.finalY - this.originY) / 100.0;
    }

    render() {
        return (
            <Model parent='#threats-layer'
                color='black'
                x={this.originX + (this.props.index * this.stepX)}
                z={this.originY + (this.props.index * this.stepY)}
                y={.1}
                angle={130} />
        );
    }
}