import React from "react";
import { Threat } from './threat'

export default class ThreatsLayer extends React.Component {
    render() {
        return <div>
            <Threat parent='#threats-layer' showLabel={this.props.showLabels} iconColor='red' circleColor='red' radius={1.5} z={0.1} x={8} y={1} />
            <Threat parent='#threats-layer' showLabel={this.props.showLabels} iconColor='red' circleColor='red' radius={1.5} z={0.2} x={8.5} y={1} />
            <Threat parent='#threats-layer' showLabel={this.props.showLabels} iconColor='red' circleColor='red' radius={1.5} z={0.3} x={8.25} y={1.25} />
        </div>;
    }
}
