import React from "react";

export class Terrain extends React.Component {
    constructor() {
        super();
        this.blob = null;
    }

    componentDidMount() {
        var blob = document.createElement('a-plane');
        blob.setAttribute('src', '#terrain')
        blob.setAttribute('position', { x: this.props.startX ?? 0, y: this.props.startY ?? 0, z: this.props.startZ ?? 0 });
        blob.setAttribute('rotation', { x: -90, y: 0, z: 0 })
        blob.setAttribute('width', this.props.width ?? 60)
        blob.setAttribute('height', this.props.height ?? 40)
        blob.setAttribute('opacity', ".4")

        var container = document.querySelector(this.props.parent);
        this.blob = container.appendChild(blob);
    }

    componentWillUnmount() {
        this.blob.parentNode.removeChild(this.blob);
    }

    render() {
        return null;
    }
}