import React from "react";

export class Model extends React.Component {
    entity = null;

    componentDidMount() {
        var entity = document.createElement('a-entity');
        entity.setAttribute('gltf-model', "url(./f18.glb)");
        entity.setAttribute('scale', '.1 .1 .1');
        entity.setAttribute('position', { x: this.props.x ?? 0, y: this.props.y ?? 0, z: this.props.z ?? 0 });
        entity.setAttribute('rotation', { x: 0, y: this.props.angle, z: 0 });

        var container = document.querySelector(this.props.parent);
        this.entity = container.appendChild(entity);
    }

    componentWillUnmount() {
        this.entity.parentNode.removeChild(this.entity);
    }

    componentDidUpdate(prevProps) {
        if (this.props.x !== prevProps.x || this.props.y !== prevProps.y || this.props.z !== prevProps.z || this.props.size !== prevProps.size) {
            this.entity.setAttribute('position', { x: this.props.x ?? 0, y: this.props.y ?? 0, z: this.props.z ?? 0 });
        }
    }

    render() {
        return null;
    }
}