import React from 'react';
import ToggleButton from '@material-ui/lab/ToggleButton';
import ZonesLayer from './zones-layer';
import ThreatsLayer from './threats-layer';
import { OrbitLayer } from './orbit-layer';
import WeatherLayer from './weather-layer';
import BordersLayer from './borders-layer';
import { Popup } from 'reactjs-popup';
import './index.css';
import { Terrain } from './terrain';

export default class WhiteboardMenu extends React.Component {


    constructor() {
        super();
        this.state = {
            loopIndex: 0,
            areZonesActive: true,
            areThreatsActive: true,
            areLabelsActive: true,
            isOrbitActive: true,
            isWeatherActive: true,
            isBordersActive: true,
            isTerrainActive: true,
            screenLocation: { left: 0, top: 0 },
            topLeft: false,
            bottomLeft: false,
            topRight: false,
            bottomRight: false,
            timer: null,
        }
    }

    componentDidMount() {
        this.timer = setInterval(() => {
            var i = this.state.loopIndex + 1;
            if (i > 100 - 1) {
                i = 0;
            }
            console.log(i)
            this.setState({ loopIndex: i })
        }, 100);

        document.addEventListener('click', (e) => {
            console.log(e)
            if (e.target.className === 'a-canvas') {
                let bodyWidth = e.view.innerWidth  / 2
                let bodyHeight = e.view.innerHeight / 2
                if (e.x < bodyWidth) {
                    if (e.y < bodyHeight) {
                        this.setState({
                            topLeft: !this.state.topLeft,
                            bottomLeft: false,
                            topRight: false,
                            bottomRight: false,
                            screenLocation: { left: e.x, top: e.y }
                        })
                    } else {
                        this.setState({
                            topLeft: false,
                            bottomLeft: !this.state.bottomLeft,
                            topRight: false,
                            bottomRight: false,
                            screenLocation: { left: e.x, top: e.y }
                        })
                    }
                } else {
                    if (e.y < bodyHeight) {
                        this.setState({
                            topLeft: false,
                            bottomLeft: false,
                            topRight: !this.state.topRight,
                            bottomRight: false,
                            screenLocation: { left: e.x, top: e.y }
                        })
                    } else {
                        this.setState({
                            topLeft: false,
                            bottomLeft: false,
                            topRight: false,
                            bottomRight: !this.state.bottomRight,
                            screenLocation: { left: e.x, top: e.y }
                        })
                    }
                }
            }
        })
    }

    componentWillUnmount() {
        if (this.timer) {
            window.clearInterval(this.timer);
        }
    }

    render() {
        return (
            <div>
                <ToggleButton value="freindly" aria-label="Freindly" selected={this.state.areZonesActive} onChange={() => {
                    this.setState({ areZonesActive: !this.state.areZonesActive })
                }} >
                    Freindly
                </ToggleButton>
                <ToggleButton value="threats" aria-label="Threats" selected={this.state.areThreatsActive} onChange={() => {
                    this.setState({ areThreatsActive: !this.state.areThreatsActive });
                }} >
                    Threats
                </ToggleButton>
                <ToggleButton value="orbit" aria-label="Orbit" selected={this.state.isOrbitActive} onChange={() => {
                    this.setState({ isOrbitActive: !this.state.isOrbitActive });
                }} >
                    Orbit
                </ToggleButton>
                <ToggleButton value="weather" aria-label="Weather" selected={this.state.isWeatherActive} onChange={() => {
                    this.setState({ isWeatherActive: !this.state.isWeatherActive });
                }} >
                    Weather
                </ToggleButton>
                <ToggleButton value="borders" aria-label="Labels" selected={this.state.isBordersActive} onChange={() => {
                    this.setState({ isBordersActive: !this.state.isBordersActive });
                }} >
                    Borders
                </ToggleButton>
                <ToggleButton value="terrain" aria-label="Terrain" selected={this.state.isTerrainActive} onChange={() => {
                    this.setState({ isTerrainActive: !this.state.isTerrainActive });
                }} >
                    Terrain
                </ToggleButton>
                <ToggleButton value="labels" aria-label="Labels" selected={this.state.areLabelsActive} onChange={() => {
                    this.setState({ areLabelsActive: !this.state.areLabelsActive });
                }} >
                    Labels
                </ToggleButton>
                {this.state.areZonesActive ? <ZonesLayer showLabels={this.state.areLabelsActive} /> : undefined}
                {this.state.areThreatsActive ? <ThreatsLayer showLabels={this.state.areLabelsActive} /> : undefined}
                {this.state.isOrbitActive ? <OrbitLayer showLabels={this.state.areLabelsActive} index={this.state.loopIndex} /> : undefined}
                {this.state.isWeatherActive ? <WeatherLayer showLabels={this.state.areLabelsActive} index={this.state.loopIndex} /> : undefined}
                {this.state.isBordersActive ? <BordersLayer showLabels={this.state.areLabelsActive} /> : undefined}
                {this.state.isTerrainActive ? <Terrain parent="#terrain-layer" /> : undefined}
                <Popup contentStyle={{ width: 300, position: "absolute", left: this.state.screenLocation.left, top: this.state.screenLocation.top }} open={this.state.topLeft} popupClass={"popup-content"}>
                    <p>Communication Frequencies</p>
                    <p>Aviation Distress : 121.500 MHz</p>
                    <p>Air Traffic Control 118.000 â€“ 121.950 MHz</p>
                    <p>Ground : 121.3 MHz</p>

                </Popup>
                <Popup contentStyle={{ width: 300, position: "absolute", left: this.state.screenLocation.left, top: this.state.screenLocation.top }} open={this.state.topRight} popupClass={"popup-content"}>
                    <p>HOSTILE</p>
                    <p>Threat Level : Midnight</p>
                    <p>Range : 50NM</p>
                    <p>TOP RIGHT</p>
                </Popup>
                <Popup contentStyle={{ width: 350, position: "absolute", left: this.state.screenLocation.left, top: this.state.screenLocation.top }} open={this.state.bottomLeft} popupClass={"popup-content"}>
                    <p>NOTAMS</p>
                    <p>AERODROME PRECISION APPROACH RADAR OUT OF SERVICE RWY 32L. 08 JUL 16:18 2021
                        UNTIL 27 AUG 23:59 2021. CREATED: 08 JUL 16:18 2021</p>
                    <p>RWY 05R/23L ALL LIGHTS OUT OF SERVICE. 08 JUL 13:54 2021 UNTIL 16 JUL 23:59 2021.
                        CREATED: 08 JUL 13:54 2021</p>
                </Popup>
                <Popup contentStyle={{ width: 300, position: "absolute", left: this.state.screenLocation.left, top: this.state.screenLocation.top }} open={this.state.bottomRight} popupClass={"popup-content"}>
                    <p>Weather</p>
                    <p>Wind : 40 Knots NE</p>
                    <p>Visibility : 5NM</p>
                    <p>*Severe Thunderstorm Warning*</p>
                </Popup>
            </div>
        );
    }
}