import React from "react";

export class WeatherBlob extends React.Component {
    constructor() {
        super();
        this.positions = [];
        this.blob = null;
    }

    componentDidMount() {
        this.setState({ x: this.props.startX, y: this.props.startY, z: this.props.startZ })
        var blob = document.createElement('a-image');
        blob.setAttribute('src', '#weather')
        blob.setAttribute('position', { x: this.props.x ?? 0, y: this.props.y ?? 0, z: this.props.z ?? 0 });
        blob.setAttribute('rotation', { x: -90, y: this.props.rotation ? 360 - this.props.rotation : 0, z: 0 })
        blob.setAttribute('width', this.props.width ?? 1)
        blob.setAttribute('height', this.props.height ?? 1)
        blob.setAttribute('material', "alphaTest: 0.5; transparent: true;")
        blob.setAttribute('opacity', ".5")

        var container = document.querySelector(this.props.parent);
        this.blob = container.appendChild(blob);
    }

    componentDidUpdate(prevProps) {
        if (this.props.x !== prevProps.x || this.props.y !== prevProps.y || this.props.z !== prevProps.z) {
            this.blob.setAttribute('position', { x: this.props.x ?? 0, y: this.props.y ?? 0, z: this.props.z ?? 0 });
        }
        if (this.props.width !== prevProps.width) {
            this.blob.setAttribute('width', this.props.width ?? 1)
        }
        if (this.props.height !== prevProps.height) {
            this.blob.setAttribute('height', this.props.height ?? 1)
        }
        if (this.props.rotation !== prevProps.rotation) {
            this.blob.setAttribute('rotation', { x: -90, y: this.props.rotation ? 360 - this.props.rotation : 0, z: 0 })
        }
    }

    componentWillUnmount() {
        this.blob.parentNode.removeChild(this.blob);
        window.clearInterval(this.timer);
    }

    render() {
        return null;
    }
}